<template>
  <div>
    <div v-for="(car, carIndex) in cars" class="row layout-margin8">
      <input v-model="car.name">
      <button @click="deleteCar(carIndex)">Delete</button>
    </div>
    <button @click="save">Save</button>
    <button @click="cars.push({})">New car</button>
  </div>
</template>
<script>
export default {
  name: 'Data',
  created () {
    if(localStorage.cars) {
      this.cars = JSON.parse(localStorage.cars);

    }
  },
  data () {
    return {
      cars: []
    }
  },
  methods: {
    save () {
      localStorage.cars = JSON.stringify(this.cars)
    },
    deleteCar (carIndex) {
      this.cars.splice(carIndex, 1);
      localStorage.cars = JSON.stringify(this.cars);
    }
  }
}
</script>