<template>
	<div>
		<div class="header">
			<div class="row w-flex x-center y-center monthSelect" v-if="selectedLocation !== 'Select Location...'">
				<div class="row h-100 w-flex x-center y-center clickable" @click="getPrev">&lt;</div> <div class="row h-100 w-flex x-center y-center clickable">{{ months[monthIndex].name }} {{ year }}</div>
				<div class="row h-100 w-flex x-center y-center clickable" @click="getNext">&gt;</div>
			</div>
			<div class="locationSelect">
				<div class="row y-center clickable h-100" @click="showLocationList = !showLocationList">
					<div class="w-flex">{{ selectedLocation }}</div>
					<div class="w-10">▾</div>
				</div>
				<div class="locationList" v-if="showLocationList">
					<div v-for="location in locationList" :key="location" class="clickable" @click="selectLocation(location)">{{ location }}</div>
				</div>
			</div>
		</div>
		<div class="x-center container" v-if="selectedLocation !== 'Select Location...'">
			<div class="column month">
				<div class="row">
					<div class="row w-flex x-center day" v-for="(day, dayIndex) in days" :class="{ 'fc-dark-red' : dayIndex === 0 }" :key="dayIndex">{{ day.substring(0, 3) }}</div>
				</div>
				<div class="row w-flex" v-for="(week, weekIndex) in month" :key="weekIndex">
					<div v-for="(date, dateIndex) in week" :key="dateIndex" :class="{ selectedDate:  monthIndex === selectedDate.month && year == selectedDate.year && date == selectedDate.date, today: monthIndex === today.month && year == today.year && date == today.date, 'fc-dark-red' : dateIndex === 0 }" class="w-flex row x-center y-center date clickable" :style="(function() {
						thisDate = calDates[selectedLocation][year + '-' + ('0' + (monthIndex + 1)).slice(-2) + '-' + ('0' + date).slice(-2)];
						if (thisDate && thisDate.img) {
							eventBgImg = '../assets/img/' + thisDate.img + '.jpg';
						}
						return styleObject(thisDate)
					})()" @click="selectDate(date, days[dateIndex])">
						{{ date }}
						<div class="dot red row x-center y-center" v-if="thisDate && thisDate.ekadashi"></div>
						<div class="dot blue row x-center y-center" v-if="thisDate && thisDate.events && ((thisDate.events.length > 0) || (thisDate.special))">
							{{ thisDate.special ? thisDate.events.length + 1 : thisDate.events.length }}
						</div>
					</div>
				</div>
			</div>
			<div class="events">
				<div class="title row x-center y-center" v-if="view !== 'search'">
					<div class="w-20 clickable h-100 row y-center x-center" @click="view = 'all';" title="View all events">
						<img src="../assets/img/icon/view-all.jpg" alt="View All" height="20">
					</div>
					<div class="w-flex">
						<div v-if="view === 'selected'">{{ selectedDate.date + ' ' + months[selectedDate.month].name + ' ' + selectedDate.year }}</div>
					</div>
					<div class="w-10 clickable h-100 row y-center x-center" @click="goTo(today)" title="Go to Today" v-if="view !== 'all'">
						<img src="../assets/img/icon/back.png" alt="Today" height="15">
					</div>
					<div class="w-10 clickable h-100 row y-center x-center" title="Search event" @click="toggleSearch">
						<img src="../assets/img/icon/search.png" alt="Search" height="15">
					</div>
					<div class="cancel clickable w-10 search row x-center y-center" @click="view = 'selected'" v-if="view === 'all'">
						<img src="../assets/img/icon/close.png" alt="Close" height="15">
					</div>
				</div>
				<div v-else class="row">
					<input type="text" v-model="searchText" id="search" class="w-flex search" placeholder="Search event">
					<div class="cancel clickable w-10 search row x-center y-center" @click="view = 'selected'">
						<img src="../assets/img/icon/close.png" alt="Close" height="15">
					</div>
				</div>
				<div v-if="view === 'selected' && selectedDate.dateDetails" class="list">
					<div class="row x-center y-center lunar">{{ selectedDate.dateDetails['lunar-day'] }}</div>
					<div class="event clickable not special" v-if="selectedDate.dateDetails.special">{{ selectedDate.dateDetails.special }}</div>
					<div class="event clickable not special11" v-if="selectedDate.dateDetails.ekadashi">{{ selectedDate.dateDetails.ekadashi }}</div>
					<a v-for="(event, eventIndex) in selectedDate.dateDetails.events" :key="eventIndex" :href="event.link" target="_blank" class="event clickable row y-center" :class="{ not: !event.link, 'fw-bold': event.link }">
						<div v-if="event.img" class="event-image" :style="{ 'background-image': 'url(' + require('../assets/img/' + event.img + '.jpg') + ')' }"></div>
						<div>{{ event.name }}</div>
					</a>
				</div>
				<div v-else-if="view === 'all'" class="list">
					<div v-for="(day, date) in calDates[selectedLocation]" :key="date" class="clickable column" @click="selectEvent(date)">
						<div style="font-weight: bold; background-color: lightyellow;" class="event">{{ date }}</div>
						<div v-for="(event, eventIndex) in day.events" :key="eventIndex" class="event">{{ event.name }}</div>
					</div>
				</div>
				<div v-else-if="view === 'search'" class="list">
					<div v-for="(event, eventIndex) in filteredEvents" :key="eventIndex" class="clickable column" @click="selectEvent(event.date)">
						<div style="font-weight: bold; background-color: lightyellow;" class="event">{{ event.date }}</div>
						<div class="event">{{ event.name }}</div>
					</div>
				</div>
				<div v-else class="list"></div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	name: 'Calendar',
	data() {
		return {
			view: 'selected',
			month: [],
			monthIndex: 0,
			today: {},
			months: [{ name: 'January', days: 31 }, { name: 'February', days: 28 }, { name: 'March', days: 31 }, { name: 'April', days: 30 }, { name: 'May', days: 31 }, { name: 'June', days: 30 }, { name: 'July', days: 31 }, { name: 'August', days: 31 }, { name: 'September', days: 30 }, { name: 'October', days: 31 }, { name: 'November', days: 30 }, { name: 'December', days: 31 }],
			days: ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'],
			dayLastIndex: 0,
			day1Index: 0,
			year: 0,
			selectedDate: { nothing: 0 },
			searchText: '',
			calDates: {},
			selectedLocation: 'Select Location...',
			showLocationList: false,
			filteredEvents: [],
			locationList: ['Bogota', 'India', 'Manila', 'Mauritius', 'Mexico', 'New York', 'Singapore', 'UK'],
			eventImages: {
				AcharyaMhj: 'Nirmal Acharya',
				GovindaMhj: 'Sundar Govinda',
				SwamiMhj: 'Bhaktivedanta',
				SridharMhj: 'Raksak Sridhar',
				SaraswatiMhj: 'Siddhanta Saraswati',
				GauraKisoraMhj: 'Paramahamsa Sri Srila Gaura Kishor',
				BhaktivinodaThakur: 'Sachchidananda Bhakti Vinod',
				JagannathMhj: 'Srila Jagannath Das Babaji',
				SriBalaram: 'Sri Baladev',
				SriKrishna: 'Janmashtami',
				SriRadha: 'Radhashtami',
				SriGaura: 'Gaura Purnima',
				SriNitai: 'Sri Nityananda Prabhu'
			}
		}
	},
	created() {
		var vm = this;
		var today = new Date();
		vm.today = {
			date: today.getDate(),
			day: today.getDay(),
			month: today.getMonth(),
			year: today.getFullYear()
		}

		let location = localStorage.location;

		if (location) {
			vm.selectLocation(location);
		}
	},
	methods: {
		styleObject(thisDate) {
			var vm = this;
			let mobOpt = vm.$parent.widthLT960 ? '-mob' : '';
			return {
				'--eventBgImg': thisDate && thisDate.img ? 'url(' + require('../assets/img/' + thisDate.img + mobOpt + '.jpg') + ')' : ''
			}
		},
		detectMob() {
			const toMatch = [
				/Android/i,
				/webOS/i,
				/iPhone/i,
				/iPad/i,
				/iPod/i,
				/BlackBerry/i,
				/Windows Phone/i
			];

			return toMatch.some((toMatchItem) => {
				return navigator.userAgent.match(toMatchItem);
			});
		},
		getMonth(date, dateIndex) {
			var vm = this,
				numOfDays = vm.months[vm.monthIndex].days;
			vm.month = [];
			if (date !== 1) {
				vm.day1Index = ((dateIndex - (date % 7)) + 1 + 7) % 7;
			} else {
				vm.day1Index = dateIndex;
			}

			fillMonth(0, 1);

			function fillMonth(weekNo, date) {
				var lastWeek = false;
				vm.month.push([]);
				for (var i = 0; i < 7; i++) {
					if ((weekNo == 0 && i < vm.day1Index) || lastWeek) {
						vm.month[weekNo].push('');
					} else if (date != numOfDays) {
						vm.month[weekNo].push(date);
						date++;
					} else {
						lastWeek = true;
						vm.dayLastIndex = i;
						vm.month[weekNo].push(date);
					}
				}
				if (!lastWeek) {
					fillMonth(weekNo + 1, date);
				}
			}
		},
		getPrev() {
			var vm = this;
			if (vm.monthIndex === 0) {
				vm.monthIndex = 11;
				vm.year--;
			} else {
				vm.monthIndex--;
			}
			vm.checkFeb();
			if (vm.day1Index === 0) {
				vm.getMonth(vm.months[vm.monthIndex].days, 6);
			} else {
				vm.getMonth(vm.months[vm.monthIndex].days, vm.day1Index - 1);
			}
		},
		getNext() {
			var vm = this;
			if (vm.monthIndex === 11) {
				vm.monthIndex = 0;
				vm.year++;
			} else {
				vm.monthIndex++;
			}
			vm.checkFeb();
			if (vm.dayLastIndex === 6) {
				vm.getMonth(1, 0);
			} else {
				vm.getMonth(1, vm.dayLastIndex + 1);
			}
		},
		checkFeb() {
			var vm = this;
			if (vm.monthIndex === 1 && (vm.year % 4) === 0) {
				vm.months[1].days = 29;
			} else {
				vm.months[1].days = 28;
			}
		},
		selectDate(date, day) {
			var vm = this,
				details;
			if (date) {
				vm.selectedDate = {
					date: date,
					month: vm.monthIndex,
					year: vm.year,
					day: day
				}
				if (vm.calDates[vm.selectedLocation][vm.year + '-' + ('0' + (vm.selectedDate.month + 1)).slice(-2) + '-' + ('0' + vm.selectedDate.date).slice(-2)]) {
					vm.selectedDate.dateDetails = vm.calDates[vm.selectedLocation][vm.year + '-' + ('0' + (vm.selectedDate.month + 1)).slice(-2) + '-' + ('0' + vm.selectedDate.date).slice(-2)];
				}
				vm.view = 'selected';
			}
		},
		goTo(date) {
			var vm = this;
			vm.monthIndex = date.month;
			vm.year = date.year;
			vm.checkFeb();
			vm.getMonth(date.date, date.day);
			vm.selectDate(date.date, date.day);
		},
		selectEvent(dateText) {
			var vm = this;
			var date = new Date(dateText);
			vm.goTo({
				date: date.getDate(),
				day: date.getDay(),
				month: date.getMonth(),
				year: date.getFullYear()
			});
		},
		toggleSearch() {
			this.view = 'search';
			setTimeout(function() {
				document.getElementById('search').focus();
			}, 100)
		},
		selectLocation(location) {
			var vm = this;
			vm.showLocationList = false;
			if (!vm.calDates[location]) {
				import('../assets/cal-data/cal-' + location)
					.then(locationImport => {
						vm.loadLocation(location, locationImport.default);
					})
			} else {
				vm.loadLocation(location);
			}
		},
		loadLocation(location, locationImport) {
			var vm = this;
			vm.selectedLocation = location;
			localStorage.location = location;
			for (let i in vm.eventImages) {
				for (let j in locationImport) {
					for (let k = 0; k < locationImport[j].events.length; k++) {
						if (locationImport[j].events[k].name.includes(vm.eventImages[i])) {
							locationImport[j].events[k].img = i;
							if (!locationImport[j].img) {
								locationImport[j].img = i;
							}
						}
					}
				}
			}
			if (!vm.calDates[location]) {
				vm.calDates[location] = locationImport;
			}
			if (vm.selectedDate && vm.selectedDate.date) {
				vm.selectDate(vm.selectedDate.date, vm.selectedDate.day);
			} else {
				vm.goTo(vm.today);
			}
		}
	},
	watch: {
		searchText() {
			var vm = this,
				keys = Object.keys(vm.calDates[vm.selectedLocation]);
			vm.filteredEvents = [];
			for (var i = 0; i < keys.length; i++) {
				var date = vm.calDates[vm.selectedLocation][keys[i]];
				for (var j = 0; j < date.events.length; j++) {
					if (date.events[j].name.toLowerCase().indexOf(vm.searchText.toLowerCase()) > -1) {
						vm.filteredEvents.push({ date: keys[i], name: date.events[j].name });
					}
				}
			}
		}
	}
}
</script>
