import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Calendar from '../views/Calendar.vue'
import Books from '../views/Books.vue'
import ListMgt from '../views/ListMgt.vue'
import Data from '../views/Data.vue'

Vue.use(VueRouter)

const routes = [{
	path: '/',
	name: 'Home',
	component: Home,
	redirect: '/calendar',
	children: [{
		path: '/books',
		name: 'Books',
		component: Books
	}, {
		path: '/calendar',
		name: 'Calendar',
		component: Calendar
	}]
}, {
	path: '/listmgt',
	name: 'ListMgt',
	component: ListMgt
}, {
	path: '/data',
	name: 'Data',
	component: Data
}]

const router = new VueRouter({
	routes
})

export default router