</template>
<script>
// @ is an alias to /src
import gurus from '../assets/js/guru-data';

export default {
	name: 'home',
	data() {
		return {
			showSidepanel: false,
			menuDisplay: 'menu-display-none',
			widthLT960: false,
			gurus: gurus
		}
	},
	created() {
		var vm = this;
		if (window.innerWidth < 860) {
			vm.widthLT960 = true;
		}
	},
	methods: {
		logOut() {
			localStorage.clear();
			location.reload();
		},
		goTo(name) {
			var vm = this;
			if (vm.$route.path != '/' + name) {
				vm.$router.push('/' + name);
			}
			vm.menuDisplay = 'menu-display-none';
		},
		getImg(code) {
			var vm = this;
			let mobOpt = vm.widthLT960 ? '-mob' : '';
			return { 'background-image': code ? 'url(' + require('../assets/img/' + code + mobOpt + '.jpg') + ')' : '' }
		}
	}
}
</script>
